import React, {useEffect, useState} from 'react';
import Image from "react-bootstrap/Image";
import axios from "axios";

const CCTCountPC = ({width, height, top, left, onResultChange}) => {
    const [inputValue, setInputValue] = useState(0); //  0

    const handleInputChange = (e) => {
        const newValue = e.target.value;
        // 
        if (/^[0-9]\d*(\.\d*)?$/.test(newValue) || newValue === '') {
            setInputValue(newValue);
        }
    };

    // 
    const calculatedResult = calculateValue(inputValue); // 

    function calculateValue(value) {
        let result = value;

        // ETH
        onResultChange(result);

        // CCT
        result = result * 100000;
        return result;
    }

    return (
        <div style={{
            position: 'absolute',
            width: width,
            height: height,
            top: top,
            left: left,
        }}>
            <div style={{
                position: 'relative',
                width: '100%',
                height: '50%',
            }}>
                <Image
                    src="images/main/pc/CCTCountBG1.png"
                    className='position-absolute'
                    style={{
                        width: '100%',
                        top: '0%',
                        left: '0%',
                    }}
                />
                {/*  */}
                <input
                    type="text"
                    value={inputValue}
                    onChange={handleInputChange}
                    style={{
                        position: 'absolute',
                        top: '48.5%',
                        left: '2.75%',
                        zIndex: 2,
                        padding: '0px',
                        color: 'white',
                        background: 'transparent',
                        border: 'none',
                        fontSize: '1.75vw',
                    }}
                />
            </div>

            <div style={{
                position: 'relative',
                width: '100%',
                height: '50%',
            }}>
                <Image
                    src="images/main/pc/CCTCountBG2.png"
                    className='position-absolute'
                    style={{
                        width: '100%',
                        top: '0%',
                        left: '0%',
                    }}
                />
                {/*  */}
                <span
                    style={{
                        position: 'absolute',
                        top: '26.5%',
                        left: '2.75%',
                        zIndex: 2,
                        color: 'white',
                        fontSize: '1.75vw',
                    }}
                >
                    {calculatedResult}
                </span>
            </div>
        </div>
    );
}

export default CCTCountPC;